import { lazy } from "react";
import { Navigate } from "react-router-dom";





//import { Navigate } from "react-router-dom";



/****Layouts*****/
const FullLayout = lazy(() => import("../layouts/FullLayout/FullLayout.jsx"));
const AuthLayout = lazy(() => import("../layouts/AutLayout/AuthLayout.jsx"));
const AuthLayoutAsistencial = lazy(() => import("../layouts/AutLayoutAsistencial/AuthLayoutAsistencial.jsx"));
/****End Layouts*****/



/*****Pages Autentication******/
const Login = lazy(() => import("../views/Auth/Login"));
const Support = lazy(() => import("../views/Auth/Support"));

const LoginAsistencia = lazy(() => import("../views/AuthAsistencial/LoginAsistencia"));
const SupportAsistencia = lazy(() => import("../views/AuthAsistencial/SupportAsistencia"));
const LoginFirstAsistencia = lazy(() => import("../views/AuthAsistencial/LoginFirstAsistencia"));




//const Dashboard1 = lazy(() => import("../views/dashboards/Dashboard1"));
const Home1 = lazy(() => import("../views/ViewsPaciente/Home/Home1"));
const Consultas = lazy(() => import("../views/ViewsPaciente/Consultas/Consultas"));
const Recetas = lazy(() => import("../views/ViewsPaciente/Recetas/Recetas"));
const Examenes = lazy(() => import("../views/ViewsPaciente/Examenes/Examenes"));
const Calendar = lazy(() => import("../views/ViewsPaciente/Calendar/Calendar"));


/*****Tables******/
const BasicTable = lazy(() => import("../views/ViewsPaciente/tables/BasicTable"));

// form elements
const ExAutoComplete = lazy(() =>
import("../views/ViewsPaciente/FormElements/ExAutoComplete")
);
const ExButton = lazy(() => import("../views/ViewsPaciente/FormElements/ExButton"));
const ExCheckbox = lazy(() => import("../views/ViewsPaciente/FormElements/ExCheckbox"));
const ExRadio = lazy(() => import("../views/ViewsPaciente/FormElements/ExRadio"));
const ExSlider = lazy(() => import("../views/ViewsPaciente/FormElements/ExSlider"));
const ExSwitch = lazy(() => import("../views/ViewsPaciente/FormElements/ExSwitch"));
// form layouts
const FormLayouts = lazy(() => import("../views/ViewsPaciente/FormLayouts/FormLayouts"));

/*****Routes******/

const ThemeRoutes = [
  {
    path: "",
    element: <FullLayout />,
    children: [
      { path: "/", element: <Navigate to="ips/home" /> },
      { path: "ips/home", exact: true, element: <Home1 /> },
      { path: "/home", element: <Home1 /> },
      { path: "/consultas", element: <Consultas /> },
      { path: "/recetas", element: <Recetas /> },
      { path: "/examenes", element: <Examenes /> },
      { path: "/calendario", element: <Calendar /> },
      { path: "tables/basic-table", element: <BasicTable /> },
      { path: "/form-layouts/form-layouts", element: <FormLayouts /> },
      { path: "/form-elements/autocomplete", element: <ExAutoComplete /> },
      { path: "/form-elements/button", element: <ExButton /> },
      { path: "/form-elements/checkbox", element: <ExCheckbox /> },
      { path: "/form-elements/radio", element: <ExRadio /> },
      { path: "/form-elements/slider", element: <ExSlider /> },
      { path: "/form-elements/switch", element: <ExSwitch /> },
    ],
  },
  {
    path: "",
    element: <AuthLayout />,
    children: [
      { path: "/autenticacion", element: <Navigate to="login" /> },
      { path: "login", exact: true, element: <Login /> },
      { path: "/login", element: <Login /> }, 
      { path: "/support", element: <Support /> },
    ],
  },
  {
    path: "",
    element: <AuthLayoutAsistencial />,
    children: [
      { path: "/autenticacionasistencial", element: <Navigate to="loginasistencial" /> },
      { path: "loginasistencial", exact: true, element: <LoginAsistencia /> },
      { path: "/loginasistencial", element: <LoginAsistencia /> }, 
      { path: "/fristloginasistencial", element: <LoginFirstAsistencia /> }, 
      { path: "/supportasistencial", element: <SupportAsistencia /> }, 
    ],
  },

];

export default ThemeRoutes;
